import Home from "./routes/Home";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Detail from "./routes/Detail";
import Tab from "./routes/Tab";
import Rating from "./routes/Rating";
const App = () => {
  return <Router>
    <Route>
      <Tab/>
    </Route>
    <Switch>
      <Route path="/rating/:select">
        <Rating/>
      </Route>
      <Route path="/movie/:id">
        <Detail/>
      </Route>
      <Route path="/">
        <Home/>
      </Route>
    </Switch>
  </Router>
}
export default App;