import React, { useEffect } from "react";
import { useState } from "react";
import { useParams } from "react-router";
import Movie from "../components/Movie";

const Rating = () => {
  const {select} = useParams()
  const [data,setData] = useState([])
  const getData = async () => {
    const json = await (await fetch(`https://yts.mx/api/v2/list_movies.json?minimum_rating=${select}&limit=10`)).json()
    setData(json.data.movies)
  }
  useEffect(()=>{
    getData()
  },[])
  // console.log(data.data.movies)
  return data===[]?<h1>Loading..</h1> :<div>
      <h3>Rating upper than {select}.0</h3>
      {data.map(movie=><Movie key={movie.id} id={movie.id} src={movie.medium_cover_image} title={movie.title} genres={movie.genres} summary={movie.summary}/>)}
    </div>
}

export default Rating