import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Movie from "../components/Movie";

function Home(){
  const [loading,setLoading] = useState(true)
  const [data,setData] = useState([])
  const [select,setSelected] = useState(1)
  const getMovie = async() => {
    const json = await (await fetch(`https://yts.mx/api/v2/list_movies.json?sort_by=year&minimum_rating=9`)).json()
    setData(json.data.movies)
    setLoading(false)
  }
  useEffect(()=>{
    getMovie()
    
  },[])
  const onChange = (e) => {
    setSelected(e.target.value)
  }
  // const onClick = (e) => {

  // }
  return <div>
    <div>
      rating  
      <select onChange={onChange}>
        {[1,2,3,4,5,6,7,8,9].map(a=><option value={a}>
          {a}
        </option>)}
      </select>
      <span ><Link to={`/rating/${select}`}> search </Link></span>
    </div>
    {loading?<h1>Loading..</h1>:data.map(movie=><Movie key={movie.id} id={movie.id} src={movie.medium_cover_image} title={movie.title} genres={movie.genres} summary={movie.summary}/>
    )}
  </div>
}
export default Home